import React, { Component } from 'react'
import Media from 'react-media';

const ModalStyle = {
    position: 'fixed', 
    zIndex: 1, 
    left: 0, 
    top: 0, 
    width: "100%", 
    height: "100%", 
    overflow: 'auto', 
    backgroundColor: "rgba(0,0,0,0.4)"
}
export default class Modal extends Component {
    render(){
    const {
        img,
        name,
        price,
        link,
        priority,
        comment
      } = this.props.item
  
      const priorityList = { '1': "–ù–µ–≤–µ—Ä–æ—è—Ç–Ω–æ —Å–∏–ª—å–Ω–æ —Ö–æ—á—É", "2": "–û—á–µ–Ω—å —Ö–æ—á—É", "3": "–û—á–µ–Ω—å —Ö–æ—á—É", "4": "–•–æ—á—É" }
      const priorityClass = { '1': "is-primary", "2": "is-success", "3": "is-success", "4": "is-error" }
      let isMobile
      return (
        <div style={ModalStyle}>
          <Media query="(max-width: 599px)">
          {matches =>
            isMobile = !!matches
          }
          </Media>
          <dialog className="nes-dialog" open style={{ margin: "1% auto", width: isMobile ? '100%' : '400px' }}>
            <p className="title">{name}</p>
            <img style={{ width: "100%"}} src={img} alt=''></img>
            <br/>
            <br/>
            <p>–ü—Ä–∏–æ—Ä–∏—Ç–µ—Ç: <span className={"nes-text " + priorityClass[priority]}>{priorityList[priority]}</span></p>
            <p>–¶–µ–Ω–∞: {price}</p>
            {comment && <p>{comment}</p>}
            <menu className="dialog-menu">
              <button className="nes-btn" onClick={() => this.props.closeModal()}>–ó–∞–∫—Ä—ã—Ç—å</button>
              <button className="nes-btn is-primary" onClick={() => window.open(link, "_blank")}>–ö—É–ø–∏—Ç—å</button>
            </menu>
          </dialog>
        </div>)
    }
}


