          <p>"–ì–æ—Å–ø–æ–¥–∏, –ø–æ—á–µ–º—É —è –Ω–µ —Ä–æ–¥–∏–ª–∞—Å—å –º–∞–ª—å—á–∏–∫–æ–º! –û—Ç–∫—Ä—ã–ª–∞ —à–∫–∞—Ñ, —á—Ç–æ –æ—Ç—Ç—É–¥–∞ –≤—ã–ø–∞–ª–æ ‚Äî –≤ —Ç–æ–º –∏ –ø–æ—à–ª–∞!"</p>
